.project {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /*  */
  border-radius: 7vh; /*  */
  padding: 50px;
  margin-bottom: 7vh;
  background: linear-gradient(to left, rgba(35, 20, 79, 0.1), rgba(3, 7, 18, 0.01));
  backdrop-filter: blur(10vh); /*  */
  -webkit-backdrop-filter: blur(10px); /* Safari  */
}

.projectImage {
  width: 77%; /* 77% */
  aspect-ratio: 16 / 9; 
  border-radius: 2vh; /*  */
  box-shadow: 0 4px 11px rgba(0, 0, 0, 0.5); /*  */
  border: 2px solid #46464657; /*  */
  margin-bottom: 3vh; /*  */
  object-fit: cover; /*  */
  object-position: center; /*  */
  overflow: hidden; 
}

.projectTitle {
  margin: 0;
  margin-bottom: 2vh; /*  */
  color: #fff; /*  */
  font-size: calc(1.2em + 0.5vw); /*  */
  font-weight: bold; /*  */
  text-shadow: 0px 0px 0.9vh rgba(0, 0, 0, 0.75); /*  */
  letter-spacing: 0.09em; /*  */
}

.projectDescription {
  margin: 0;
  margin-bottom: 2vh; /*  */
  color: #c4c4c4; /*  */
  max-width: 70%; 
  font-size: calc(0.9em + 0.2vw); /*  */
  text-shadow: 0px 0px 0.9vh rgba(0, 0, 0, 1.9); /*  */
}

.projectLink {
  color: #c489f5; /*  */
  text-decoration: none; /*  */
  font-weight: bold;
  font-size: calc(0.7em + 0.2vw); 
  text-shadow: 0px 0px 0.3vh rgba(0, 0, 0, 0.55); /*  */
}

@media (max-width: 768px) {

  .project {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /*  */
    border-radius: 7vh; /*  */
    padding: 30px;
    margin-bottom: 7vh;
    margin-left: 3.7vw;
    margin-right: 3.7vw;
    background: linear-gradient(to left, rgba(35, 20, 79, 0.37), rgba(3, 7, 18, 0.37));
    backdrop-filter: blur(11vh); /*  */
    -webkit-backdrop-filter: blur(11px); /* Safari  */
  }
  
  .projectImage {
    width: 90%; /* 77% */
    aspect-ratio: 16 / 9; 
    border-radius: 2vh; /*  */
    box-shadow: 0 4px 11px rgba(0, 0, 0, 0.5); /*  */
    border: 2px solid #46464657; /*  */
    margin-bottom: 3vh; /*  */
    object-fit: cover; /*  */
    object-position: center; /*  */
    overflow: hidden; 
  }
  .projectDescription {
    margin: 0;
    margin-bottom: 2vh; /*  */
    color: #c4c4c4; /*  */
    max-width: 97%; 
    font-size: calc(0.9em + 0.2vw); /*  */
    text-shadow: 0px 0px 0.9vh rgba(0, 0, 0, 1.9); /*  */
  }
}